.about-container {
    background: #fffbe6;
    color: #ff69b4;
    padding: 3rem 2rem;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Comic Sans MS', 'Arial Rounded MT Bold', cursive;
    position: relative;
    overflow-x: hidden;
  }
  
  .about-title {
    font-size: 3rem;
    margin-bottom: 1rem;
    text-align: center;
  }
  
  .about-text {
    font-size: 1.2rem;
    max-width: 700px;
    text-align: center;
    margin-bottom: 1rem;
    line-height: 1.6;
  }
  
  .about-welcome {
    font-size: 2rem;
    margin-top: 2rem;
    color: #f4c430;
    text-shadow: 1px 1px 2px #ffaaff;
  }
  
  .dog-row {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    background-color: #fff0b3;
  }
  
  .dog-img {
    height: 80px;
    margin: 0.5rem;
    transition: transform 0.3s ease;
  }